import { required, minLength, between } from "vuelidate/lib/validators";

const MIN = {
    text: 4,
    phone: 12
}

// Vue
export const Vue__validationObjectPrivate = {
    // Имя
    name: {
        required,
        minLength: minLength(MIN.text)
    },
    // Фамилия
    surname: {
        required,
        minLength: minLength(MIN.text),
    },
    // Отчество
    lastname: {
        minLength: minLength(MIN.text),
    },
    birth: {
        required
    },
    phone: {
        required,
        minLength: minLength(MIN.phone)
    },
    gender: {},
    clients: {
        required
    },
    doctor: {}
}

// JS

export const checkValue = (value, nameElement) => {
    let regXP;
    switch (nameElement) {
        case 'name':
        case 'surname':
        case 'lastname':
            regXP = /\W/i;
            break;
        case 'phone':
            regXP = /\d/i
            break;
        default:
            return null;
    }
    let checkValue = '';
    for (let i = 0; i < value.length; i++) {
        if (i == 0 && nameElement === 'phone') {
            checkValue += "+"
        }
        if (regXP.test(value[i])) {
            if (nameElement === 'phone') {
                if (i < MIN.phone) {
                    checkValue += value[i]
                }
            } else {
                checkValue += value[i]
            }
        }
    }
    // Нельзя удалить первую цифру
    if (value.length == 1 && nameElement === 'phone') {
        checkValue = '+7';
    }
    return checkValue;
}